@import './helpers/fonts.scss';
@import './helpers//color-scheme.scss';

.heading2{
  font-family : $PlayfairDisplay;
  font-size   : 35px;
  font-weight : bold;
  color       : #394A66;
}
.heading3{
  color       : #828282;
  font-family : $SFProText;
  font-size   : 24px;
}
.p{
  color       : #4F4F4F;
  font-family : $SFProText;
  font-size   : 16px;
}
.heading1{
  font-family : $PlayfairDisplay;
  font-size   : 45px;
  font-weight : bold;
  color       : #394A66;
}
.link{
  font-family : $SFProText;
  font-size   : 14px;
  color       : #ADABBA;
}
.linkspan{
  font-family : $SFProText;
  font-size   : 14px;
  color       : #394A66;
}
#AddProperty{
  display: flex;
}
#DashboardMain{
  display: flex;
  .MuiList-padding{
    padding-top: 0px !important;
  }
  .dashboardProperty{
    font-family: 'SF Pro Text';
    font-weight: bold;
    font-size  : 20px;
    color      : #4F4F4F;
    
  }
  .MuiOutlinedInput-notchedOutline{
    border          : none      !important;
  }
  .empty{
    width     : 386px;
    height    : 182px;
    text-align: center;
    margin    : auto;
    margin-top: 168px;
  }
  .emptyOne{
    font-family : 'SF Pro Text';
    font-size   : 24px;
    font-weight : 700;
    color       : #394A66;
  }
  .emptyTwo{
    font-family: 'SF Pro Text';
    font-size: 18px;
    font-weight: 400;
    color: #ADABBA;
  }
}


#SideBar{

  .sidenav {
    height          : 100vh;
    width           : 77px;
    z-index         : 1;
    top             : 0;
    left            : 0;
    background-color: #F2E9E1;
    overflow-x      : hidden;
    text-align      : -webkit-center;
    position        : sticky;
    }
    @media screen and (max-height: 450px) {
      .sidenav    {padding-top: 15px;}
      .sidenav a  {font-size: 18px;}
    }
    .dot {
      height          : 54px;
      width           : 54px;
      background-color: none;
      border-radius   : 50%;
      margin-bottom   : 45px;
    }
    .fixdot{
      height          : 54px;
      width           : 54px;
      background-color: #394A66;
      border-radius   : 50%;
      margin-bottom   : 45px;
      filter          : drop-shadow(0px 4px 10px #B3B3B3);
    }
    .dot:focus-within{
      background-color: #F7F2ED;
      transition      : 0.5s;
    }
    .image{
      height        : 50px;
      width         : 32px;
      margin-top    : 35px;
      margin-bottom : 90px;
    }

}  



/////////////////////////////
#PropertyType{
  width       : 585px;
  margin-top  : 16px;
  margin-left : 24px;
  .dropdown{
    border-radius   : 15px;
    background-color: #F5F7F9;
    color           : #8FA6BC;
  }
}
#PropertyDetail{
  width       : 585px;
  margin-top  : 16px;
  margin-left : 24px;
  .dropdown{
    border-radius   : 15px;
    background-color: #F5F7F9;
    color           : #8FA6BC;
  }
}
.upload{
  margin-left       : 24px                !important;
  background-color  : white             !important;
  color             : #394A66           !important;
  box-shadow        : none                !important;
  border            : 1px solid #394A66 !important;
  width             : 212px               !important;
  height            : 65px                !important;
  border-radius     : 10px                !important;
  text-transform    : capitalize          !important;
  font-size         : 18                  !important;
  font-family       : $SFProText          !important;
  font-weight       : 600                 !important;
}
#PropertyImage{
  .inputClass{
    display: none;
  }
  .imageClass{
    width         : 500px;
    height        : 400px;
    border-radius : 16px;
    margin-left   : 16px;
    margin-bottom : 16px;
  }
}
#Stepper{
  display: flex;
  .btn-next{
    height            : 65px;
    width             : 279px;
    border-radius     : 10px;
    text-transform    : capitalize;
    font-size         : 18px;
    font-family       : $SFProText;
    font-weight       : 600;
    float             : right;
    background-color  : #F2E9E1;
    box-shadow        : none;
    color             : #394A66;
    outline-style     : none
  }
  .btn-previous{
    height              : 65px;
    background-color    : white;
    box-shadow          : none;
    color               : #394A66;
    outline-style       : none;
    text-decoration-line: underline;
    font-family         : $SFProText;
    font-size           : 18px;
    text-transform      : capitalize            
  }
  .btn-main-div{
    width: 585px;
  }
  /////////
  .MuiStepIcon-root.MuiStepIcon-active{
    color : #F2E9E1   !important;
    border: none        !important;
  }
  .MuiStepIcon-text{
    fill        : #394A66 !important;
    font-family : $SFProText !important;
  }
  .MuiStepIcon-root{
    width         : 56px                !important;
    height        : 56px                !important;
    color         : white             !important;
    border        : 1px solid #E3E3E3 !important;
    border-radius : 50px                !important;
  }
  .MuiStepIcon-root.MuiStepIcon-completed{
    fill: #F2E9E1;
    border: none !important;
  }
  .MuiButton-root.Mui-disabled{
    color: #ADABBA !important;
  }
  
  .MuiStep-horizontal{
    padding-right: 168px !important;
  }
  .MuiStepConnector-root{
    flex: none !important ;
  }
  .MuiTypography-body2{
    font-size   : 14px        !important;
    font-family : $SFProText  !important;
    font-weight : 600         !important;
  }
  .MuiStepLabel-label.MuiStepLabel-active{
    color: #394A66 !important;
  }
}
