.link{
    text-decoration: none !important;
    // width: 100vw;


     .items{
        width: 100%;
        display: flex;
        flex-direction: row;
        height: 98.6px;
        background-color:#FFFFFF;
        align-items: center;
        vertical-align: middle;

        .imageP{
            padding-left: 13px;


            .image{

            }

        }
        .conetent{

            display         : flex;
            flex-direction  : column;

                .maincontent{
                      display   : flex;
                flex-direction  : row;    
                            
                            .heading{
                                    color: #394A66;
                                    font-size: 16px;
                                }
                            .time{
                                    font-size: 12px;
                                    color: #828282;
                                }

                }
            .subconten{
                display         :   flex;
                flex-direction  :   row;
                    .messages{
                        font-size: 14px;
                        color: #ADABBA;                    }

                    }
                    .no_message_circle{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        background-color: #394A66;
                        color: white;
                        width: 20px;
                        height: 20px;         
                        border-radius: 50%;
                        text-decoration: none;
                       
                    }


            
        }
     }
}