$colors: (
    'cF2E9E1'   : $cF2E9E1,
    'cF5F7F9'   : $cF5F7F9,
    'c394A66'   : $c394A66,
    'c4f4f4f'   : $c4f4f4f,
    'cF7F2ED'   : $cF7F2ED,
    'c828282'   : $c828282,
    'c708FC2'   : $c708FC2,
    'cF2F2F2'   : $cF2F2F2,
    'cADABBA'   : $cADABBA,
    'cE4D0BF'   : $cE4D0BF,
    'cD4B59B'   : $cD4B59B,
    'c8FA6BC'   : $c8FA6BC,
    'cFFFFFF'   : $cFFFFFF,
    'cBDBDBD'   : $cBDBDBD,
);


@each $name, $color in $colors {
    .color-#{$name} {
        color: #{$color} !important;
    }

    .bg-color-#{$name} {
        background-color: #{$color} !important;
    }
}

