#ImageSlider{
    width   : 600px;
    height  : 400px;
}

#Details{
    display: flex;
    
    .dotDetails {
        height          : 48px;
        width           : 48px;
        background-color: #E3E3E3;
        border-radius   : 50%;
        display         : inline-block;
      }
      .colOne{
        align-self:center
      }
      .colTwoRow{
        float       : right;
        align-items : center;
      }
      .btn{
        color       :white;
        font-size   :18px;
        font-weight :600;
        line-height :21.48px;
        width       :168px;
        height      :53px
      }
      .textOne{
          color         : #ADABBA;
          font-size     : 16px;
          font-family   : 'SF Pro Text';
      }
      .textTwo{
          color         : #394A66;
          font-size     : 16px;
          font-family   : 'SF Pro Text';
          font-weight   : 500;
      }
      .imgSvg{
          width : 7px;
          height: 14px;
      }


      .secondMain{
          font-family   : 'SF Pro Text';
          font-size     : 24px;
          font-weight   : 700;
          line-height   : 28.64px;
          color         : #333333;
      }
      .secondSub{
          font-family   : 'SF Pro Text';
          font-size     : 40px;
          font-weight   : 800;
          line-height   : 47.73px;
          color         : #333333;
      }
      .secondSmall{
          font-family   : 'SF Pro Text';
          font-size     : 16px;
          font-weight   : 400;
          line-height   : 19.09px;
          color         : #ADABBA;
      }
      .colParent{
          .col{
              padding: 0;
          }
      }
      .carousel .thumb{
          width     : 144px !important;
          padding   : 0     !important;
      }
      .features{
          font-family: 'SF Pro Text';
          font-size  : 20px;
          color      : #333333;
          font-weight: 600;
          width      : fit-content;
      }
      .par{
        .row{
            margin-left: 24px !important;
        }
      }
      .featureDetail{
          font-family: 'SF Pro Text';
          font-size  : 16px;
          color      : #394A66;
          align-self : center;
      }
      .map-container{
        overflow        : hidden;
        padding-bottom  : 30%;
        position        : relative;
        height          : 0;
        }
        .map-container iframe{
        left            : 0;
        top             : 0;
        height          : 90%;
        width           : 90%;
        position        : absolute;
        border-radius   : 16px;
        }

}


#DashboardCard{
    .dotDetails{
        height          : 48px;
        width           : 48px;
        background-color: #E3E3E3;
        border-radius   : 50%;
        display         : inline-block;
    }
}