body {
  margin                  : 0;
  margin-left             : auto;
  margin-right            : auto;
  font-family             : $SFProText;
  color                   : black;
  font-size               : 16px;
  font-weight             : 400;
  -webkit-font-smoothing  : antialiased;
  -moz-osx-font-smoothing : grayscale;
  scroll-behavior         : smooth;
  line-height             : normal;
}
.main-heading{
  font-family : $PlayfairDisplay;
  color       : $c394A66;
  font-style  : normal;
  font-weight : bold;
  font-size   : 45px;
}
.sub-heading{
  font-family : $PlayfairDisplay;
  color       : $c394A66;
  font-style  : normal;
  font-weight : bold;
  font-size   : 35px;
}
.small-heading{
  font-family : $PlayfairDisplay;
  color       : $c394A66;
  font-style  : normal;
  font-weight : bold;
  font-size   : 24px;
}
.norm-heading{
  font-family : $SFProText;
  font-size   : 16px;
  color       : $c4f4f4f;
}
.para{
  font-family : $SFProText;
  font-size   : 12px;
  line-height : 14px;
  color       : $cADABBA;
}
.p{
  font-family : $SFProText;
  font-size   : 16px;
  color       : "#4F4F4F";
}
/* ---------------------------------------------------
    Material Customization
----------------------------------------------------- */
#DashboardCard{
  .row{
    margin-left : 0     !important;
  }
  .col{
    padding: 0 !important;
  }
  .col-8{
    padding: 0 !important;
  }
  .MuiCheckbox-colorSecondary.Mui-checked{
    color: #394A66 !important;
  }
  .MuiIconButton-colorSecondary:hover{
    background-color: #E3E3E3 !important;
  }
}
////////dashboard border remove
.MuiPaper-elevation1{
  box-shadow: none !important;
}
.MuiListItem-button:hover{
  .colDisplay{
    display: block !important;
  }
}
///////////dialog box heading
.alertParent{
  .MuiTypography-h6{
    font-family : $SFProText  !important;
    font-size   : 24px        !important;
    font-weight : bold        !important;
    color       : #394A66   !important;
    margin-top  : 24px ;
  }
}

